/* App.css */

body {
  background-color: #1a1a1a;
  color: #fff;
  overflow-y: hidden;
  scrollbar-color: #006400 #1a1a1a;
  scrollbar-width: thin;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #2a2a2a;
  opacity: 0.9;
  background-size: 23px 23px;
}

.output {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.result {
  font-size: 36px;
  font-weight: bold;
  color: #fff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; /* Add a modern font */
  margin-top: 10px; /* Add margin for spacing */
}

.input {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #333;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

label {
  margin: 20px 0;
}

/* Improved styling for the input */
input[type="range"] {
  margin-top: 15px;
  width: 100%;
  height: 10px;
  -webkit-appearance: none;
  background-color: #444;
  border-radius: 10px;
  outline: none;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-color: #006400;
  border-radius: 50%;
  cursor: pointer;
}

input[type="range"]::-webkit-slider-thumb:hover {
  background-color: #004d00;
}

button {
  margin-top: 20px;
  padding: 15px;
  cursor: pointer;
  background-color: #006400;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #004d00;
}

.input input {
  margin: 10px 0;
  padding: 0px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #555;
  border-radius: 5px;
  background-color: #444;
  color: #fff;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.input input:focus {
  outline: none;
  border-color: #006400;
}

.dollar-input {
  display: flex;
  align-items: center;
}

.dollar-input span {
  background-color: #444;
  border: 1px solid #555;
  border-radius: 5px 0 0 5px;
  padding: 12px;
  color: #fff;
  font-size: 16px;
}

.dollar-input input {
  margin: 10px 0;
  padding: 12px;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #555;
  border-radius: 0 5px 5px 0;
  background-color: #444;
  color: #fff;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.dollar-input input:focus {
  outline: none;
  border-color: #006400;
}
.label-container {
  display: flex;
  align-items: center;
}

.label-container input {
  width: 50px; /* Set a fixed width for the percentage input */
  margin-left: 10px; /* Add some margin between the range input and percentage input */
}
.info-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  align-items: center;
}

.info-icon,
.close-icon {
  background-color: #006400;
  color: #fff;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-left: 10px;
}

.info-popup {
  position: relative;
  bottom: 20px;
  right: 20px;
  background-color: #333;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
  z-index: 999;
}